export async function authenticateRazorpay(){
    try {
        const response = await cruspoFetch(baseURL.authenticationBaseUrl+routes.authenticateRazorpay,{
            mode:'cors',
            headers:header,
        });
        if(response.ok){
            var data = await response.json();
            window.open(data.data, "_blank");
        }else{
            return false;
        }
    } catch (error) {
        console.log(error);

    }
}
