export default class CruspoTimeCalculator{
    
    constructor(hour=9,min=0,timeFormat='12-hour'){
        this.startHour = parseInt(hour);
        this.startMin = parseInt(min);
        this.currentHour = parseInt(hour);
        this.currentMin = parseInt(min);
        this.timeFormat = timeFormat;
    }

    addDuration(duration){
        duration=parseInt(duration);
        if(duration<60)
        {
            this.currentMin=this.currentMin+duration;
        }else if(duration>=60){
            this.currentHour = this.currentHour+60/60;            
            this.currentMin = this.currentMin+(duration-(60/60)*60);
        }
        if(this.currentMin>=60){
            this.currentHour++;
            this.currentMin=this.currentMin-(60/60)*60;
        }
        if(this.currentHour>12 && this.timeFormat==='12-hour'){
            this.currentHour=1
        }
        if(this.currentHour>=24){
            this.currentHour=0
        }
    }

    getCurrentTime(){
            if(this.currentHour.toString().length===1){
                return this.currentMin===0?`0${this.currentHour}:${this.currentMin}0`:`0${this.currentHour}:${this.currentMin}`
            }else{
                return this.currentMin===0?`${this.currentHour}:${this.currentMin}0`:`${this.currentHour}:${this.currentMin}`
            }
    }

    generateListBasedOnDuration(duration){
        var timeList = [];

        while(!timeList.includes(this.getCurrentTime())){
            timeList.push(this.getCurrentTime());
            this.addDuration(duration);
        }
        return timeList;
    }


    getTimeList(startTime="09:00 P.M.",duration=45,endTime="05:00 P.M."){
        var newList = [];
        var flag = startTime.substr(6,4)=='A.M.'?0:1;
        var timelist = this.generateListBasedOnDuration(duration);
        newList.push(`${timelist[0]} ${startTime.substr(6,4)}`);
        for(let i=1;i<timelist.length;i++){
            if(timelist[i].substr(0,2)==12 && timelist[i-1].substr(0,2)!=12){
                flag = flag^1;
            }
            if(!flag){
                if(this._isAfter(`${timelist[i]} A.M.`,endTime)){
                    break;
                }else{
                    newList.push(`${timelist[i]} A.M.`);
                }
            }else{
                if(this._isAfter(`${timelist[i]} P.M.`,endTime)){
                    break;
                }else{
                    newList.push(`${timelist[i]} P.M.`);
                }
            }
            
        }
        return newList;
    }

    durationToMinutes({hour,min}){
        var minutes = hour*60;
        minutes = minutes+min;
        return minutes;
    }

    // _isBefore(time1,time2){

    //     function getHour(time){
    //         return time.substr(0,1)==0?time.substr(1,1):time.substr(0,2);
    //     }

    //     function getMin(time){
    //         console.log(time.substr(3,1));
    //         return time.substr(3,1)==0?time.substr(1,1):time.substr(0,2);
    //     }

    //     if(getHour(time1)<getHour(time2)){
    //         return true;
    //     }else{
    //         return false;
    //     }
    // }

    _isAfter(time1,time2){
        var flag = 0;

        function getHour(time){
            return time.substr(0,1)==0?time.substr(1,1):time.substr(0,2);
        }

        function getMin(time){
            return time.substr(3,1)==0?time.substr(4,1):time.substr(3,2);
        }

        function getTimeMode(time){
            return time.substr(6,4);
        }
        
        if(getTimeMode(time1)=='A.M.'){
            if(getHour(time1)>getHour(time2)){
                flag = 0;
            }else if(getHour(time1)==getHour(time2)){
                if(getMin(time1)>getMin(time2)){
                    flag = 1;
                }
            }
        }else{
            if(getHour(time1)<getHour(time2)){
                flag=0;
            }else if(getHour(time1)==getHour(time2)){
                if(getMin(time1)>getMin(time2)){
                    flag = 1;
                }
            }
        }
        return flag;
    }

}

