import React, { Component } from 'react'
import * as authenticationServices from '../../../services/authenticationServices';

export default class ForgetPassword extends Component {
    
    async sendResetEmail(e){
        e.preventDefault();
        const response = await authenticationServices.sendPasswordResetLink({
            email:this.state.email
        });
    }


    render() {
        return (
            <div className="forget-password">
                <div>
                    <form onSubmit={(e)=>this.sendResetEmail(e)}>
                        <input className="cruspo-input" type="email" placeholder="Your registered Email" onChange={(e)=>{this.setState({email:e.currentTarget.value})}}/>
                        <button className="cruspo-button" type="submit">Send Reset Link</button>

                    </form>
                </div>
            </div>
        )
    }
}
